export default {
  name: "navigationBar",
  props: {
    title: {
      type: String,
      default: "标题"
    },
	clickTitle: {
	  type: String,
	  default: "按钮"
	},
	isClick: {
	  type: Boolean,
	  default: false
	},
	isBack: {
	  type: Boolean,
	  default: true
	}
  },
  data() {
    return {
		autoplay: true,
		videoCtx:'',
		platform: 0
	};
  },
  created() {
	// console.log('created')
	
  },
  mounted() {
	// 设备样式兼容
	this.platform = uni.getStorageSync('platform');
  },
  methods: {
	  // 返回上一级
	  goBack(){
		  uni.navigateBack({
		  	delta:1
		  })
		  //this.$emit("goBack", false);
	  },
	  doClick(){
		  this.$emit("doClick", false);
	  }
  }
};
